<template>
	<div>
		<div class="pagebox">
		<div class="newarticle" style="padding: 10px 0;">
			<Form>
				<FormItem>
			        <div class="newtitle">
			        	<Input  size="large" :maxlength="50" placeholder="请输入文章标题(5-50个字符)" v-model="desc"  @input="descInput"></Input>
			        	<div class="datalength">{{remnant}}/{{maxlength}}</div>
			        </div>
			        <div id="editor">
					    <mavon-editor v-model="content"  style="height: 100%" :toolbars="toolbars"/>
					</div>
				</FormItem>
				<FormItem label="文章封面" :label-width="70">
		            <div class="upoladbox">
						<div class="upoladbox_img">
							<Upload name="item_img" multiple type="drag" :format="['jpg','jpeg','png']" :max-size="5120" action="/posts/" :show-upload-list="false" :before-upload="selectImg" :on-format-error="handleFormatError" :on-exceeded-size="maxerror">
						        <div style="padding: 40px 0" v-model="backimg">
						            <Icon type="ios-cloud-upload" size="52" style="color: #3399ff"></Icon>
						            <p>点击选择或者拖动图片到此次上传</p>
						        </div>
						    </Upload>
						</div>
						<div class="upoladbox_tip">图片大小不超过5M，尺寸大小为9：6<p><Icon type="information-circled"></Icon>不选择图片，系统自动获取文章内的图片作为封面</p></div>
					</div>
		        </FormItem>
			    <FormItem label="文章分类" :label-width="70">
		            <Cascader :data="data" change-on-select style="width:200px"></Cascader>
		        </FormItem>
		        <FormItem label="文章标签" :label-width="70">
		            <Tag v-for="item in count" :key="item" :name="item" closable @on-close="tagColse">{{ item }}</Tag>
    				<Button icon="ios-plus-empty" type="dashed" size="small" v-show="tagbtnisShow" @click="tagAdd">添加标签</Button>
    				<Input v-model="tagvalue" size="small" v-show="tagisShow" placeholder="" style="width: 100px" @on-blur="tagblur" @on-enter="tagblur"></Input>
		        </FormItem>
		        <FormItem label="是否原创" :label-width="70">
		            <Checkbox v-model="single" size="large">申明原创
		            </Checkbox>
		            <Tooltip content="非原创内容禁止添加原创标签" placement="right">
			            <Icon type="information-circled" size="14"></Icon>
			        </Tooltip>
		        </FormItem>
		        <FormItem label="原文链接" :label-width="70">
		            <Input  placeholder="http://" style="width: 500px"></Input>
		        </FormItem>
				<div class="sub_btn">
					<Button type="primary" :loading="loading" @click="toLoading" icon="ios-cloud-upload-outline">
				        <span v-if="!loading">发&nbsp;&nbsp;布</span>
				        <span v-else>发布中...</span>
			    	</Button> 
			    	<Button type="warning" icon="eye"  @click="alertbox = true">预&nbsp;&nbsp;览</Button>
			    	<Button type="success" icon="ios-paper">存草稿</Button>
				</div>
		    </Form>
		</div>
		</div>
	</div>
</template>

<script>
 export default {
   data () {
     return {
       loading: false,
       value: '',
       desc: '',
       remnant: 0,
       maxlength: 50,
       single: false,
       alertbox: false,
       tagisShow: false,
       tagbtnisShow: true,
       tagvalue: '',
       content: '',
       toolbars: {
         bold: true, // 粗体
         italic: true, // 斜体
         header: true, // 标题
         underline: true, // 下划线
         strikethrough: true, // 中划线
         quote: true, // 引用
         ol: false, // 有序列表
         ul: false, // 无序列表
         link: false, // 链接
         imagelink: true, // 图片链接
         code: true, // code
         table: true, // 表格
         fullscreen: true, // 全屏编辑
         help: true, // 帮助
         undo: true, // 上一步
         redo: true, // 下一步
         trash: true, // 清空
         save: true, // 保存（触发events中的save事件）
         navigation: false, // 导航目录
         alignleft: false, // 左对齐
         aligncenter: false, // 居中
         alignright: false, // 右对齐
         subfield: false, // 单双栏模式
         preview: false // 预览
       },
       backimg: '',
       count: [],
       data: [{
         value: '1',
         label: '人工智能',
         children: [
           {
             value: '2',
             label: '深度学习'
           },
           {
             value: '3',
             label: '机器语言'
           },
           {
             value: '4',
             label: '云计算'
           },
           {
             value: '5',
             label: '大数据'
           }
         ]
       },
       {
         value: '6',
         label: '编程语言',
         children: [
           {
             value: '7',
             label: 'Python编程'
           },
           {
             value: '8',
             label: 'Java编程'
           },
           {
             value: '9',
             label: 'C/C++编程'
           },
           {
             value: '10',
             label: '.Net编程'
           }
         ]
       },
       {
         value: '10',
         label: '前端开发',
         children: [
           {
             value: '12',
             label: '前端基础'
           },
           {
             value: '13',
             label: 'AngularJS'
           },
           {
             value: '14',
             label: 'Vue.js'
           },
           {
             value: '15',
             label: 'React.js'
           },
           {
             value: '16',
             label: '前端工具'
           }
         ]
       },
       {
         value: '17',
         label: '移动开发',
         children: [
           {
             value: '18',
             label: 'Android'
           },
           {
             value: '19',
             label: 'IOS'
           },
           {
             value: '20',
             label: 'Unity 3D'
           },
           {
             value: '21',
             label: 'Cocos2d-x'
           }
         ]
       }]
     }
   },
   methods: {
     toLoading () {
       this.loading = true
     },
     descInput () {
       var txtVal = this.desc.length
       console.log(txtVal)
       this.remnant = txtVal
     },
     selectImg () {
       const check = this
       console.log(check)
     },
     handleFormatError (file) {
       this.$Message.error({
         content: '上传的文件类型不正确！'
       })
     },
     maxerror (file) {
       this.$Message.error({
         content: '上传的文件超过限制！'
       })
     },
     tagAdd () {
       this.tagisShow = !this.tagisShow
       this.tagbtnisShow = !this.tagbtnisShow
     },
     tagblur () {
       if (this.tagvalue.length) {
         this.count.push(this.tagvalue)
       }
       this.tagisShow = !this.tagisShow
       this.tagbtnisShow = !this.tagbtnisShow
       this.tagvalue = ''
     },
     tagColse (event, name) {
       const index = this.count.indexOf(name)
       this.count.splice(index, 1)
     }
   }
}
</script>

<style>
#editor {
    margin: auto;
    height: 680px;
}
</style>


